body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.About, .Article, .Blog, .NoMatch {
  text-align: center;
}

.clearfix {
  float: none;
}

.header > ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

.header > ul > li {
  float: left;
}

.header > ul > li > a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.header > ul > li > a:hover {
  background-color: #111;
}

.Blog ul {
  list-style-type: none;
  text-align: left;
}

.About p {
  font-size: 22px;
}

.Photo img {
  max-width: 200px;
  max-height: 200px;
}

@media (min-width: 768px) {
  .Blog .LeftPanel {
    float: left;
    width: 25%;
    border: 1px solid #ccc;
  }

  .Blog .RightPanel {
    float: left;
    width: 74%;
    border: 1px solid #ccc;
  }
}



